import Color from "color";

const warning = "#ffd294";
const light = "#B3D9E3";
const lightest = "#E6F1F4";
const darkWarning = Color(warning).darken(0.45).string();

const Branding = {
  primary: "#5890A7",
  light: light,
  lighter: "#D9E5EA",
  lightest: lightest,
  lightBg: "#f8f8f8",
  warning: "#ffd294",
  dark: "#172C42",
  slotColors: {
    free: "#a0ffae",
    booked: "#ff929c",
    off: "#dfdfdf",
    hover: light,
    selected: lightest,
    shadowColor: (color: string): string =>
      new Color(color).darken(0.5).alpha(0.5).toString(),
    shadow: (color: string, iOffset?: number, iBlur?: number): string => {
      let offset = iOffset ? iOffset : 1;
      let blur = iBlur ? iBlur : 2;
      return (
        offset +
        "px " +
        offset +
        "px " +
        blur +
        "px " +
        new Color(color).darken(0.5).alpha(0.5).toString()
      );
    },
  },
  slotColorsDot: {
    free: "#50af56",
    booked: "#ea1542",
    off: "#dfdfdf",
  },
  text: {
    primary: "#000000",
    secondary: "#777777",
    warning: darkWarning,
  },
  fontSize: {
    small: 13,
  },
  fontFamily: {
    heading: "",
    body: '"Roboto", "Segoe UI", "Helvetica", "Arial", open_sansregular,Helvetica Neue,Helvetica,-apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif',
  },
  shadow: {
    niceShadow: "0 0 25px #00000011",
  },
  borderRadius: {
    niceRadius: 10,
  },
  transition: "all 0.1s",
};

