import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import PageTitle from "../common/PageTitle";
import mock from "../../images/details.png";
import BackendFieldSet from "../forms/BackendFieldSet";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
import SubmitButton from "../forms/SubmitButton";
import {
  WorkerWorkflowDropdown,
  WorkerWorkflowStatuses,
} from "../../config/WorkerWorkflowDropdown";

interface Props extends RouteComponentProps {}

export default function UpdateRequestPage(props: Props): ReactElement {
  // const defaultStatus = Faker.random.arrayElement(WorkerWorkflowStatuses);
  const defaultStatus = WorkerWorkflowStatuses[0];

  const fields: IBackendField[] = [
    {
      name: "requestStatus",
      variant: "select",
      defaultValue: defaultStatus,
      selectOptions: WorkerWorkflowDropdown,
    },
    {
      name: "notes",
      variant: "textarea",
    },
    {
      name: "notify",
      label: "Notify customer of status update",
      variant: "checkbox",
      defaultValue:true,
    },
    // {
    //   name: "hide",
    //   label: "Hide this worker from other customers",
    //   variant: "checkbox",
    // },

  ];
  return (
    <Box>
      <PageTitle title="Update Request" />
      <Box>
        <img
          src={mock}
          alt="Details"
          style={{
            display: "block",
            width: "100%",
          }}
        />
      </Box>
      <Box>
        <Grid container>
          <Grid item md={6}>
            <BackendFieldSet header="Agency Actions" fields={fields} md={12} />
          </Grid>
        </Grid>
        <SubmitButton title="Save" navigate={props.navigate} />
      </Box>
    </Box>
  );
}
