import React, { ReactElement } from "react";
import logo from "../../images/logo.svg";

interface Props {
  styles?:React.CSSProperties;
}

export default function Logo(props: Props): ReactElement {
  return (
    <img
      src={logo}
      style={{
        display: "block",
        maxWidth: "100%",
        margin: "0",
        maxHeight: 40,
        ...props.styles
      }}
      alt="Estreeh Logo"
    />
  );
}
