import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  TextField,
} from "@material-ui/core";
import { Link, RouteComponentProps, navigate } from "@reach/router";
import axios, { AxiosResponse } from "axios";
import React, { ReactElement, useState } from "react";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import Branding from "../../config/Branding";
import { IApiLoginBody } from "../../../../estreeh-common-library/build/interfaces/IApiLoginBody";
import { UserRole } from "../../../../estreeh-common-library/build/types/UserRole";

interface Props extends RouteComponentProps {
  setUser: React.Dispatch<React.SetStateAction<IUser | undefined>>;
  getUser: () => Promise<AxiosResponse<IResponse<IUser | undefined>>>;
}

export default function LoginPage(props: Props): ReactElement {
  const [loggingIn, setLoggingIn] = useState(false);
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");

  const allowedRoles: UserRole[] = ["Agency", "Agency Manager"];

  const loginFail = (reason?: string) => {
    setLoggingIn(false);
    if (reason !== undefined) console.log("Login error:", reason);
  };
  const onClick = () => {
    setLoggingIn(true);
    const postBody: IApiLoginBody = {
      email,
      password,
    };
    axios
      .post<IResponse<IUser | undefined>>(
        process.env.REACT_APP_API_ROOT + "/auth/login",
        postBody,
        { withCredentials: true }
      )
      .then((result) => {
        const user = result.data.data;
        if (
          user !== undefined &&
          user.agencyId !== undefined &&
          allowedRoles.includes(user.role)
        ) {
          props
            .getUser()
            .then((result) => {
              props.setUser(result.data.data);
              navigate("/");
            })
            .finally(() => {
              loginFail();
            });
        } else {
          loginFail("User is not authorized to use this platform.");
        }
      })
      .catch((reason) => {
        loginFail(reason);
      });
  };

  return (
    <Box>
      <Box
        p={3}
        maxWidth={440}
        marginLeft="auto"
        marginRight="auto"
        my={5}
        boxShadow={Branding.shadow.niceShadow}
        borderRadius={Branding.borderRadius.niceRadius}
      >
        <form>
          <TextField
            id="username"
            label="Email"
            variant="outlined"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            id="username"
            label="Password"
            variant="outlined"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <Box py={0}>
            <Grid container spacing={1}>
              <Grid item xs={6}>
                <FormControlLabel
                  control={
                    <Checkbox name="checkedB" color="primary" size="small" />
                  }
                  label="Keep me Logged In"
                />
              </Grid>
              <Grid item xs={6}>
                <Box textAlign="right">
                  <Link to="reset-password">
                    <Box lineHeight="38px">Forgot password?</Box>
                  </Link>
                </Box>
              </Grid>
            </Grid>
          </Box>
          <Box py={3} textAlign="center">
            <Button
              disabled={loggingIn}
              variant="contained"
              color="primary"
              onClick={onClick}
            >
              Login
            </Button>
          </Box>
        </form>
      </Box>
    </Box>
  );
}
