import { Button } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import SuccessBigMessage from "../common/SuccessBigMessage";

interface Props extends RouteComponentProps {}

export default function SuccessWorkerPage(props: Props): ReactElement {
  const navigate =
    props.navigate !== undefined ? props.navigate : (s: string) => {};
  return (
    <SuccessBigMessage bigText="New worker added successfully">
      <Button
        variant="contained"
        color="primary"
        style={{ marginRight: 16 }}
        onClick={() => {
          navigate("../add");
        }}
      >
        Add Another
      </Button>
      <Button
        variant="contained"
        color="primary"
        onClick={() => {
          navigate("..");
        }}
      >
        Workers List
      </Button>
    </SuccessBigMessage>
  );
}
