import { Button } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import SuccessBigMessage from "../common/SuccessBigMessage";

interface Props extends RouteComponentProps {}

export default function SuccessAgencyPage(props: Props): ReactElement {
  const navigate =
    props.navigate !== undefined ? props.navigate : (s: string) => {};
  return (
    <SuccessBigMessage bigText="Agency settings updated successfully">
      <Button
        variant="contained"
        color="primary"
        onClick={() => {
          navigate("/panel/dashboard");
        }}
      >
        Done
      </Button>
    </SuccessBigMessage>
  );
}
