import { Box, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import {HeaderVariant} from "../../../../estreeh-common-library/build/types/HeaderVariant";

interface Props {
  header?: string;
  variant?: HeaderVariant;
}

export default function FieldSetHeader(props: Props): ReactElement {
  const variant = props.variant !== undefined ? props.variant : "normal";
  const small = variant === "small";
  const normal = variant === "normal";

  return (
    <>
      {props.header && (
        <Box py={small ? 1 : 2}>
          <Typography variant={normal ? "h6" : undefined}>
            {props.header}
          </Typography>
        </Box>
      )}
    </>
  );
}
