import { Box, Grid, GridSize, Typography } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import { HeaderVariant } from "../../../../estreeh-common-library/build/types/HeaderVariant";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
import BackendField from "./BackendField";
import FieldSetHeader from "./FieldSetHeader";

interface Props {
  header?: string;
  headerVariant?: HeaderVariant;
  fields?: IBackendField[];
  children?: React.ReactNode;
  md?: GridSize;
  xs?: GridSize;
  formik?: FormikProps<any>;
  disabled?: boolean;
  helpText?: React.ReactNode;
}

export default function BackendFieldSet(props: Props): ReactElement {
  return (
    <Box>
      <FieldSetHeader header={props.header} variant={props.headerVariant} />
      <Box mb={2}>
        <Typography color="primary">
          {props.helpText}
        </Typography>
      </Box>
      <Grid container spacing={2} alignItems="center">
        {props.fields !== undefined &&
          props.fields.map((field) => (
            <BackendField
              formik={props.formik}
              field={field}
              key={field.name.toString()}
              defaultMd={props.md}
              defaultXs={props.xs}
              size="small"
              disabled={props.disabled}
            />
          ))}
        {props.children !== undefined && (
          <Grid item xs={props.xs} md={props.md}>
            {props.children}
          </Grid>
        )}
      </Grid>
    </Box>
  );
}
