import React, { ReactElement } from "react";
import { RouteComponentProps } from "@reach/router";
import { Box, Container, Link, Typography } from "@material-ui/core";
import Branding from "../../config/Branding";

interface Props extends RouteComponentProps {
  title: string;
  subtitle?: string;
  children?: React.ReactNode;
}

export default function ErrorPage(props: Props): ReactElement {
  // navigate("/panel/myrequests");
  return (
    <Container>
      <Box m={5} p={3} boxShadow={Branding.shadow.niceShadow}>
        <Typography variant="h1">{props.title}</Typography>
        <Typography variant="h5">{props.subtitle}</Typography>

        <Box py={2}>
          <Box mb={1}>{props.children}</Box>
          <Box>
            <Typography>
              <Link href="/">Back to homepage.</Link>
            </Typography>
          </Box>
        </Box>
      </Box>
    </Container>
  );
}
