import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import PageTitle from "../common/PageTitle";
import BackendFieldSet from "../forms/BackendFieldSet";
import { jobTitles } from "../../config/FilterOptions";
import { IBackendField } from "../../../../estreeh-common-library/src/interfaces/IBackendField";
import IWorkerTwoYear from "../../../../estreeh-common-library/src/interfaces/IWorkerTwoYear";
import {
  Avatar,
  Checkbox,
  FormControlLabel,
  FormGroup,
  Grid,
} from "@material-ui/core";
import SubmitButton from "../forms/SubmitButton";
import { IBackendFieldSelectOption } from "../../../../estreeh-common-library/src/interfaces/IBackendFieldSelectOption";
import { nationalities } from "../../config/Nationalities";

interface Props {
  formik: FormikProps<any>;
  id?: string;
  disabled: boolean;
  worker: Partial<IWorkerTwoYear>;
  templateFields: IBackendField[];
}

export default function AddEditWorkerForm({
  formik,
  disabled,
  id,
  worker,
  templateFields,
}: Props): ReactElement {
  const mainFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "name",
    },
    {
      name: "referenceId",
    },

    {
      name: "jobTitle",
      variant: "select",
      selectOptions: jobTitles.map((t) => ({
        value: t,
      })),
    },
    {
      name: "monthlySalary",
    },
    {
      name: "contractPeriod",
    },
    {
      name: "phone",
    },
    // {
    //   name:"hide",
    //   label:"Hide this worker",
    //   variant:"checkbox"
    // }
    {
      name: "isLocalTransfer",
      variant: "checkbox",
    },
  ];

  const localTransferFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "localTransferReason",
      variant: "textarea",
    },
  ];

  const fileFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "uploadSmallPicture",
      variant: "file",
      avatarWidth: 80,
      buttonIcon: "photo_camera",
    },
    {
      name: "uploadBigPicture",
      variant: "file",
      avatarWidth: 200,
      buttonIcon: "photo_camera",
    },
  ];

  const agencyOnlyFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "agencyRemarks",
      variant: "textarea",
    },
  ];

  const personalFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "nationality",
      variant: "select",
      selectOptions: nationalities.map((n) => ({
        value: n,
      })),
    },
    {
      name: "experienceYears",
      type: "number",
    },

    {
      name: "educationalLevel",
    },
    {
      name: "dateOfBirth",
      type: "date",
    },
    {
      name: "maritalStatus",
      variant: "select",
      selectOptions: [{ value: "Single" }, { value: "Married" }],
    },
    {
      name: "age",
      type: "number",
    },
    {
      name: "weightKg",
    },
    {
      name: "complexion",
    },
    {
      name: "religion",
    },

    // {
    //   name: "livingTown",
    // },
    {
      name: "numberOfChildren",
      type: "number",
    },
    {
      name: "heightCm",
    },

    {
      name: "remarks",
      variant: "textarea",
      md: 9,
    },
  ];

  const languageOptions: IBackendFieldSelectOption[] = [
    {
      value: "Poor",
    },
    {
      value: "Fair",
    },
    {
      value: "Fluent",
    },
  ];

  const languageFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "languageSkillEnglish",
      variant: "select",
      selectOptions: languageOptions,
    },
    {
      name: "languageSkillArabic",
      variant: "select",
      selectOptions: languageOptions,
    },
  ];

  const skills = [
    "Baby Sitting",
    "Cooking",
    "Nursing",
    "Elder Caring",
    "Washing",
    "Ironing",
  ];

  function picHelpClick() {
    alert(
      "To change picture please use the Upload Picture buttons near the end of this page."
    );
  }

  return (
    <form onSubmit={formik.handleSubmit}>
      <Grid container spacing={2}>
        <Grid item>
          <Avatar
            src={process.env.REACT_APP_IMAGES_ROOT + "" + worker.smallPicture}
            variant="rounded"
            style={{
              width: 90,
              height: 90,
              cursor: "help",
            }}
            onClick={picHelpClick}
          />
        </Grid>
        <Grid item xs={9}>
          <PageTitle
            title={
              id === undefined
                ? "Add a New Worker"
                : "Edit Worker: " + worker.name
            }
          />
        </Grid>
      </Grid>
      <BackendFieldSet
        fields={mainFields}
        header="Main Information"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={localTransferFields}
        md={7}
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={templateFields}
        header="Process Template"
        // formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={personalFields}
        header="Personal Details"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={languageFields}
        header="Language Skills"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        header="Skills"
        xs={12}
        formik={formik}
        disabled={disabled}
      >
        <FormGroup row>
          {skills.map((skill) => (
            <FormControlLabel
              control={<Checkbox name={"skill_" + skill} color="primary" />}
              label={skill}
            />
          ))}
        </FormGroup>
      </BackendFieldSet>
      <BackendFieldSet header="Pictures" xs={12}>
        <Grid container>
          <Grid item xs={4}>
            <Avatar
              src={process.env.REACT_APP_IMAGES_ROOT + "" + worker.smallPicture}
              variant="rounded"
              style={{
                width: 160,
                height: 160,
                cursor: "help",
              }}
              onClick={picHelpClick}
            />
          </Grid>
          <Grid item xs={4}>
            <Avatar
              src={process.env.REACT_APP_IMAGES_ROOT + "" + worker.bigPicture}
              variant="rounded"
              style={{
                width: 240,
                height: 320,
                cursor: "help",
              }}
              onClick={picHelpClick}
            />
          </Grid>
        </Grid>
      </BackendFieldSet>
      <BackendFieldSet
        fields={fileFields}
        formik={formik}
        disabled={disabled}
        // header="Picture Uploads"
        // helpText="Use the below buttons to change the current pictures."
      />
      <BackendFieldSet
        fields={agencyOnlyFields}
        header="Agency Only Information (Hidden)"
        md={7}
        formik={formik}
        disabled={disabled}
      />
      <SubmitButton
        title={id === undefined ? "Create Worker" : "Save Changes"}
        errors={formik.errors}
        type="submit"
        disabled={disabled}
      />
    </form>
  );
}
