import { Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns } from "@material-ui/data-grid";
import { RouteComponentProps } from "@reach/router";
import { ReactElement, useEffect } from "react";
import IWorkerTwoYear from "../../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import { Edit } from "@material-ui/icons";
import PageTitle from "../common/PageTitle";
import { useState } from "react";
import { IRequest } from "../../../../estreeh-common-library/build/interfaces/IRequest";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import axios from "axios";
import dateFormat from "dateformat";

interface Props extends RouteComponentProps {}

export default function MyRequestsPage(props: Props): ReactElement {
  //TODO: "New requests" page or filter
  //TODO: "Pending for Action" page or filter

  const [requests, setRequests] = useState<IRequest[]>([]);

  const columns: GridColumns = [
    {
      field: "created",
      headerName: "Date",
      sortable: true,
      width: 140,
      valueFormatter: (params) => {
        return dateFormat(params.row.created,"dd mmm yy");
      }
    },
    {
      field: "REF",
      sortable: false,
      width: 130,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.referenceId;
      },
    },
    {
      field: "Worker Name",
      sortable: true,
      width: 200,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.name;
      },
    },
    {
      field: "status",
      headerName: "Status",
      sortable: true,
      width: 180,
    },
    {
      field: "fullName",
      headerName: "Applicant Name",
      sortable: true,
      width: 250,
    },

    {
      field: "mobileNumber",
      headerName: "Mobile",
      sortable: true,
      width: 200,
    },
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Actions",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                if (props.navigate) props.navigate("update");
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },

    // {
    //   field: "specialNotes",
    //   sortable: true,
    //   width: 200,
    // },
  ];

  useEffect(() => {
    console.log("Preparing to get Requests");
    axios
      .get<IResponse<IRequest[]>>(
        process.env.REACT_APP_API_ROOT + "/be/requests",
        { withCredentials: true }
      )
      .then((requests) => {
        console.log(`Got ${requests.data.data.length} requests`);
        setRequests(requests.data.data);
      });
  }, [setRequests]);
  // const applications: IApplicationForm[] = ApplicationFormFactory.buildList(14);

  return (
    <Box>
      <PageTitle title="My Requests" />
      <Box height={400}>
        <DataGrid
          disableSelectionOnClick={true}
          rows={requests.map((v) => {
            const a = {
              ...v.applicationForm,
              trackingCode: v.trackingCode,
              requestId: v.id,
              id: v.applicationForm._id,
              workerTwoYear: v.workerTwoYear,
              status: v.status.title,
              created: v.created,
            };
            return a;
          })}
          columns={columns}
          pageSize={5}
          // checkboxSelection
        />
      </Box>
    </Box>
  );
}
