import { Box, Icon, Link } from "@material-ui/core";
import { Link as RouterLink } from "@reach/router";
import React, { ReactElement } from "react";
import { INavItem } from "../../../../estreeh-common-library/build/interfaces/INavItem";

interface Props {
  navItem: INavItem;
  my: number;
  mx: number;
}

export default function LeftNavItem(props: Props): ReactElement {
  const nav = props.navItem;
  const itemStyles: React.CSSProperties = {
    display: "block",
    color: "white",
    padding: "2px 8px",
    // margin: "20px 0",
    textDecoration: "none",
  };

  let title: React.ReactNode = (
    <Box>
      {nav.icon && (
        <Icon fontSize="small" style={{ marginRight: 6 }}>
          {nav.icon}
        </Icon>
      )}
      {nav.title}
    </Box>
  );

  return (
    <Box ml={props.mx} my={props.my}>
      {nav.to && (
        <RouterLink style={{ ...itemStyles }} to={nav.to}>
          {title}
        </RouterLink>
      )}
      {!nav.to && (
        <Link
          href="#"
          style={{
            ...itemStyles,
          }}
        >
          {title}
        </Link>
      )}
      {props.navItem.children !== undefined && (
        <Box fontSize="90%">
          {props.navItem.children?.map((navItem) => (
            <LeftNavItem
              my={0.5}
              mx={5}
              navItem={navItem}
              key={navItem.title + "_" + navItem.to}
            />
          ))}
        </Box>
      )}
    </Box>
  );
}
