import React, { ReactElement } from "react";
import { RouteComponentProps } from "@reach/router";
import { Link, Typography } from "@material-ui/core";
import ErrorPage from "./ErrorPage";

interface Props extends RouteComponentProps {}

export default function UnauthorizedPage(props: Props): ReactElement {
  return (
    <ErrorPage title="401" subtitle="❌ Unauthorized">
      <Typography>
        Unauthorized or session has expired. Please make sure you have <Link href="/login">logged in</Link>.
      </Typography>
    </ErrorPage>
  );
}
