import { Box, Container, Grid, Typography, useTheme } from "@material-ui/core";
import React, { ReactElement } from "react";
import { RouteComponentProps } from "@reach/router";
import Logo from "../common/Logo";
import Branding from "../../config/Branding";
import { useMediaQuery } from "@material-ui/core";

interface Props extends RouteComponentProps {
  children?: React.ReactNode;
}

export default function LoginLayout(props: Props): ReactElement {
  const white = "#fff";
  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up("md"));
  const gradientDir = isMd ? "right" : "bottom"
  return (
    <Grid container>
      <Grid item md={5} xs={12}>
        <Box
          px={4}
          bgcolor={Branding.lightest}
          style={{
            backgroundImage: `linear-gradient(to ${gradientDir}, ${Branding.lightest} 75%, ${white})`,
          }}
          height={isMd ? "100vh" : undefined}
        >
          <Box py={isMd ? 6 : 3} style={{ textShadow: "0 0 8px #ffffff" }}>
            <Logo
              styles={{
                maxHeight: isMd ? 120 : 50,
                marginBottom: 8 * 3,
              }}
            />
            <Typography variant={isMd ? "h4" : "h5"}>Estreeh Management Portal</Typography>
            <Typography>
              Please login to access your management portal.
            </Typography>
          </Box>
        </Box>
      </Grid>
      <Grid item md={7} xs={12}>
        <Container>
          {isMd && (
            <Box py={4}>
              <Logo
                styles={{
                  maxHeight: 50,
                  margin: "0 auto",
                }}
              />
            </Box>
          )}
          <Box>{props.children}</Box>
        </Container>
      </Grid>
    </Grid>
  );
}
