import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import axios from "axios";
import React, { ReactElement, useEffect, useState } from "react";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import PageTitle from "../common/PageTitle";
import DashboardCard from "./DashboardCard";
import { IApiDashboardSummaryResponse } from "../../../../estreeh-common-library/build/interfaces/IApiDashboardSummaryResponse";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import IAgency from "../../../../estreeh-common-library/src/interfaces/IAgency";
// import IAgency from "../../../../estreeh-common-library/build/interfaces/IAgency";

interface Props extends RouteComponentProps {
  user: IUser;
}

export default function DashboardPage(props: Props): ReactElement {
  const [dashSummary, setDashSummary] = useState<
    IApiDashboardSummaryResponse | undefined
  >(undefined);
  const [agency, setAgency] = useState<IAgency | null>(null);

  useEffect(() => {
    axios
      .get<IResponse<IApiDashboardSummaryResponse>>(
        process.env.REACT_APP_API_ROOT + "/be/dashboard",
        { withCredentials: true }
      )
      .then((result) => {
        setDashSummary(result.data.data);
        console.log("Got dashboard summary", result.data.data);
      });
    axios
      .get<IResponse<IAgency>>(process.env.REACT_APP_API_ROOT + "/be/agency", {
        withCredentials: true,
      })
      .then((result) => {
        setAgency(result.data.data);
        console.log("Got agency");
      });
  }, [setDashSummary]);

  return (
    <Box>
      <Grid container direction="row-reverse" spacing={2}>
        <Grid item md={3} xs={12}>
          <Box textAlign="center">
            <Avatar
              src={process.env.REACT_APP_IMAGES_ROOT + "" + agency?.logo}
              variant="rounded"
              style={{
                width: 260,
                height: 220,
                marginLeft:"auto",
                marginRight:"auto"
              }}
            />
            <Typography>{agency?.title}</Typography>
            <Typography>{agency?.titleAr}</Typography>
            <Typography>CR: {agency?.cr}</Typography>
          </Box>
        </Grid>
        <Grid item md={9} xs={12}>
          <PageTitle title={`Welcome, ${props.user.fullName}`} />
          <Typography>Welcome to the Estreeh agency portal.</Typography>
          <Typography>Please use the left menu to navigate.</Typography>
          <Box p={3}>
            <Grid container spacing={2} alignItems="center">
              <DashboardCard
                title="New Requests"
                value={dashSummary?.newRequests}
              />
              <DashboardCard
                title="Pending Requests"
                value={dashSummary?.pendingRequests}
              />
              <DashboardCard
                title="My Workers"
                value={dashSummary?.myWorkers}
              />
              <DashboardCard
                title="Customer Reviews"
                value={dashSummary?.customerReviews}
              />
            </Grid>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}
