import { FormikErrors } from "formik";
import React, { ReactElement } from "react";

interface Error {
  key: string;
  value:
    | string
    | FormikErrors<any>
    | string[]
    | FormikErrors<any>[]
    | undefined;
}

interface Props {
  errors: FormikErrors<any>;
  style?: React.CSSProperties;
}

export default function FormErrors(props: Props): ReactElement {
  const hasErrors = Object.keys(props.errors).length > 0;
  let errors: Error[] = [];
  if (hasErrors) {
    for (const e in props.errors) {
      errors.push({ key: e, value: props.errors[e] });
    }
  }
  return (
    <ul
      style={{
        margin: 0,
        padding: 0,
      }}
    >
      {errors.map((error) => (
        <li
          style={{
            listStylePosition:"inside",
            color: "red",
            fontSize: 11,
            ...props.style,
          }}
        >
         <strong>{error.key}</strong> {error.value}
        </li>
      ))}
    </ul>
  );
}
