import {
  Avatar,
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  GridSize,
  Icon,
  InputAdornment,
  InputLabelProps,
  MenuItem,
  Switch,
  TextField,
  Typography,
} from "@material-ui/core";
import { FieldInputProps, FormikProps } from "formik";
import React, { ReactElement, useMemo } from "react";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
import { IBackendFieldSelectOption } from "../../../../estreeh-common-library/build/interfaces/IBackendFieldSelectOption";

interface Props {
  field: IBackendField;
  defaultXs?: GridSize;
  defaultMd?: GridSize;
  size?: "medium" | "small";
  formik?: FormikProps<any>;
  disabled?: boolean;
}

export default function BackendField(props: Props): ReactElement {
  const name = props.field.name.toString();
  const label = useMemo(
    () =>
      props.field.label !== undefined
        ? props.field.label
        : CamelToSentence(name),
    [props.field.label, name]
  );

  const id = "field_" + name;
  const type = props.field.type !== undefined ? props.field.type : "text";
  const variant =
    props.field.variant !== undefined ? props.field.variant : "text";

  const xsDefault = props.defaultXs !== undefined ? props.defaultXs : 12;
  const mdDefault = props.defaultMd !== undefined ? props.defaultMd : 4;
  const xs = props.field.xs !== undefined ? props.field.xs : xsDefault;
  const md = props.field.md !== undefined ? props.field.md : mdDefault;

  const size = props.size !== undefined ? props.size : "medium";

  const labelProps: Partial<InputLabelProps> = useMemo(
    () => ({
      shrink: true,
    }),
    []
  );

  const fieldVariant = props.field.readOnly ? "outlined" : "outlined";

  const rowsDefault = props.field.variant === "textarea" ? 3 : undefined;
  const rows =
    props.field.textareaLines !== undefined
      ? props.field.textareaLines
      : rowsDefault;

  const numberMinimum =
    props.field.numberMinimum !== undefined ? props.field.numberMinimum : 0;

  const onChange = useMemo(
    () => (props.field.onChange ? props.field.onChange : () => {}),
    [props.field.onChange]
  );

  const prefixAdornment: React.ReactNode = useMemo(
    () =>
      props.field.prefix !== undefined ? (
        <InputAdornment position="start">{props.field.prefix}</InputAdornment>
      ) : undefined,
    [props.field.prefix]
  );
  const suffixAdornment: React.ReactNode = useMemo(
    () =>
      props.field.suffix !== undefined ? (
        <InputAdornment position="end">{props.field.suffix}</InputAdornment>
      ) : undefined,
    [props.field.suffix]
  );

  const fieldProps: Partial<FieldInputProps<any>> = useMemo(
    () =>
      props.formik !== undefined
        ? props.formik.getFieldProps(name)
        : {
            onChange: onChange,
            name: name,
            value: props.field.value,
            checked: props.field.value,
            onBlur: () => {},
          },
    [onChange, name, props.field.value, props.formik]
  );

  //FIXME: On submit the errors dont show for fields that have long names (more than 1 word)

  const error =
    props.formik !== undefined &&
    props.formik.touched[name] &&
    props.formik.errors[name] !== undefined;

  const helperText =
    props.formik !== undefined &&
    props.formik.touched[name] &&
    props.formik.errors[name];

  const selectOptions: IBackendFieldSelectOption[] | undefined = useMemo(
    () =>
      props.field.variant === "select"
        ? props.field.selectOptions.length > 0
          ? props.field.selectOptions
          : [
              // {
              //   value: "loading",
              //   title: "Loading",
              // },
            ]
        : undefined,
    [props.field]
  );

  const menuRender = useMemo(
    () =>
      selectOptions !== undefined &&
      selectOptions.map((option) => {
        const title =
          option.title !== undefined
            ? option.title
            : CamelToSentence(option.value);
        return (
          <MenuItem key={"opt_" + option.value} value={option.value}>
            {title}
          </MenuItem>
        );
      }),
    [selectOptions]
  );
  // const checkboxOnChange = () => {
  //   setChecked(!checked);
  //   if (props.field.onChange !== undefined) {
  //     // props.field.onChange();
  //     // setChecked(props.field.value)
  //   } else {
  //   }
  // };

  // const selectOnChange = (event: React.ChangeEvent<HTMLInputElement>) => {
  //   setSelectValue(event.target.value);
  // };

  const checkboxControl = useMemo(
    () =>
      variant === "switch" ? (
        <Switch
          // checked={props.field.value}
          color="primary"
          // defaultValue={props.field.defaultValue}
          {...fieldProps}
        />
      ) : (
        <Checkbox
          // checked={props.field.value}
          color="primary"
          // defaultValue={props.field.defaultValue}
          {...fieldProps}
        />
      ),
    [fieldProps, variant]
  );

  const fieldRender = useMemo(
    () => (
      <>
        {((variant !== "file" &&
          variant !== "switch" &&
          variant !== "checkbox" &&
          props.field.variant !== "select") ||
          (props.field.variant === "select" &&
            selectOptions !== undefined &&
            selectOptions?.length > 0)) && (
          <TextField
            id={id}
            color="primary"
            variant={fieldVariant}
            type={props.field.type}
            label={label + (props.formik === undefined ? " [UNBOUND]" : "")}
            size={size}
            select={variant === "select"}
            multiline={variant === "textarea"}
            rows={rows}
            InputLabelProps={type === "date" ? labelProps : undefined}
            // defaultValue={props.field.defaultValue}
            disabled={props.field.disabled || props.disabled}
            inputProps={{
              min: numberMinimum,
              step: props.field.step,
            }}
            InputProps={{
              startAdornment: prefixAdornment,
              endAdornment: suffixAdornment,
              readOnly: props.field.readOnly,
              classes: props.field.readOnly
                ? {
                    notchedOutline: "notched-outline-no-border",
                  }
                : undefined,
            }}
            error={error}
            helperText={helperText}
            {...fieldProps}
          >
            {menuRender}
          </TextField>
        )}

        {(variant === "switch" || variant === "checkbox") && (
          <FormControlLabel
            control={checkboxControl}
            label={label}
            {...fieldProps}
          />
        )}
        {variant === "file" && (
          <Button variant="contained" component="label">
            {props.field.buttonIcon !== undefined && (
              <Icon style={{ marginRight: 8 }}>{props.field.buttonIcon}</Icon>
            )}
            {label}
            <input
              type="file"
              id={id}
              name={name}
              hidden
              onChange={(e) => {
                if (e.currentTarget.files !== null) {
                  const file = e.currentTarget.files[0];
                  if (props.formik !== undefined) {
                    console.log(file);
                    props.formik.setFieldValue(name, file);
                  }
                }
              }}
            />
          </Button>
        )}
        {variant === "file" && false && (
          <>
            <label htmlFor={id}>
              <Box mb={1}>
                <Typography color="textSecondary">{label}</Typography>
              </Box>
              <Avatar
                variant="rounded"
                style={{
                  width: props.field.avatarWidth,
                  height: props.field.avatarWidth,
                }}
              />
            </label>
            <input
              accept="image/*"
              style={{ display: "none" }}
              id={id}
              type="file"
              {...fieldProps}
            />
          </>
        )}
      </>
    ),
    [
      checkboxControl,
      error,
      fieldProps,
      fieldVariant,
      helperText,
      id,
      label,
      labelProps,
      menuRender,
      name,
      numberMinimum,
      prefixAdornment,
      props.disabled,
      props.field.avatarWidth,
      props.field.buttonIcon,
      props.field.disabled,
      props.field.readOnly,
      props.field.step,
      props.field.type,
      props.field.variant,
      props.formik,
      rows,
      selectOptions,
      size,
      suffixAdornment,
      type,
      variant,
    ]
  );
  return (
    <Grid item xs={xs} md={md}>
      {fieldRender}
    </Grid>
  );
}

export function CamelToSentence(text: string | number) {
  if (text !== undefined) {
    const parsed: string = typeof text === "string" ? text : text.toString();
    var result = parsed.replace(/([A-Z])/g, " $1");
    return result.charAt(0).toUpperCase() + result.slice(1);
  }
  throw new Error("Text is undefined");
}
