import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import LeftNav from "../navigation/LeftNav";
import TopNav from "../navigation/TopNav";

interface Props extends RouteComponentProps {
  user: IUser;
  setUser: React.Dispatch<React.SetStateAction<IUser | undefined>>;
  children?: React.ReactNode;
}

export default function AdminPanelLayout(props: Props): ReactElement {
  return (
    <Box height="100%">
      <Grid
        container
        direction="column"
        style={{
          height: "100%",
        }}
      >
        <Grid item xs={12}>
          <Grid
            container
            alignItems="stretch"
            style={{
              height: "100%",
            }}
          >
            <Grid item md={2} xs={2}>
              <LeftNav />
            </Grid>
            <Grid item md={10} xs={10}>
              <TopNav user={props.user} setUser={props.setUser} />
              <Box px={2} pb={2} pt={12}>
                {props.children}
              </Box>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
}
