import { Box, Button, Grid } from "@material-ui/core";
import { Formik } from "formik";
import React, { ReactElement, useState } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import { IRegionConfiguration } from "../../../../estreeh-common-library/build/interfaces/IRegionConfiguration";
import Branding from "../../config/Branding";
import ProcessTemplateCard from "./ProcessTemplateCard";
import * as Yup from "yup";
import axios from "axios";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { toast } from "react-toastify";
import FormErrors from "../forms/FormErrors";

interface Props {
  process: IProcessTemplate;
  region: IRegionConfiguration;
  newItem: boolean;
  loadProcessTemplates: Function;
}

export default function ProcessTemplate(props: Props): ReactElement {
  const [editing, setEditing] = useState(props.newItem);
  const [saving, setSaving] = useState(false);
  const count = props.process.countAssignedWorkers;
  const smallFontSize = "0.7rem";
  const minAmount = 5;
  const minMonths = 0.25;

  return (
    <Box
      boxShadow={Branding.shadow.niceShadow}
      borderRadius={Branding.borderRadius.niceRadius}
      border={editing ? "2px dashed blue" : "1px solid " + Branding.lighter}
      p={1}
      my={1}
    >
      <Formik
        initialValues={props.process}
        validationSchema={Yup.object({
          title: Yup.string().min(3,"Minimum 3 characters").required("Required"),
          accommodationFeesBhd: Yup.number().required("Required"),
          governmentFeesBhd: Yup.number().required("Required"),
          insuranceFeesBhd: Yup.number().required("Required"),
          otherFeesBhd: Yup.number().required("Required"),
          vatFeesBhd: Yup.number().required("Required"),
          processFeesBhd: Yup.number().min(minAmount,`Minimum ${minAmount} BHD`).required("Required"),
          otherFeesLabel: Yup.string().required("Required"),
          agencyId: Yup.string().optional(),
          countAssignedWorkers: Yup.number().optional(),
          created: Yup.date().optional(),
          modified: Yup.date().optional(),
          processDurationMinimumMonths: Yup.number().min(minMonths,`Minimum ${minMonths} Months`).required("Required"),
          processDurationMaximumMonths: Yup.number().min(minMonths,`Minimum ${minMonths} Months`).required("Required"),
          totalFeesBhd: Yup.number().optional(),
          vatPercentage: Yup.number().optional(),
          _id: Yup.string().optional(),
          id: Yup.string().optional(),
        })}
        onSubmit={(formValues, { setSubmitting }) => {
          setSubmitting(true);
          setSaving(true);
          const isNew = formValues._id === undefined;
          const method = isNew ? "POST" : "PUT";
          const url = process.env.REACT_APP_API_ROOT + "/be/process-templates";

          axios
            .request<IResponse<IProcessTemplate>>({
              method,
              url,
              data: formValues,
              withCredentials: true,
            })
            .then((result) => {
              toast.success(result.data.message);
              setEditing(false);
              setSaving(false);
              setSubmitting(false);
              // props.loadProcessTemplates();
            })
            .catch((reason) => {
              toast.error(reason.message);
              setSaving(false);
              setSubmitting(false);
            });
        }}
      >
        {(formik) => (
          <form onSubmit={formik.handleSubmit}>
            <Grid container spacing={2} alignItems="flex-end">
              <Grid item md={10} xs={12}>
                <ProcessTemplateCard
                  process={props.process}
                  region={props.region}
                  editing={editing}
                  saving={saving}
                  formik={formik}
                />{" "}
              </Grid>
              <Grid item md={2} xs={12}>
                <Box p={1}>
                  <Box p={1} textAlign="center" fontSize={smallFontSize}>
                    Assigned Workers: {count}
                  </Box>
                  {editing && (
                    <>
                      <FormErrors
                        errors={formik.errors}
                        style={{ fontSize: smallFontSize }}
                      />
                      <Box
                        my={1}
                        textAlign="center"
                        fontSize={smallFontSize}
                        color="blue"
                        fontStyle="italic"
                      >
                        {Object.keys(formik.errors).length === 0 && (
                          <>
                            Please save your changes
                            {count > 0
                              ? `, they will be applied automatically to ${count} workers`
                              : ""}
                          </>
                        )}
                      </Box>
                      <Button
                        fullWidth={true}
                        color="primary"
                        variant="outlined"
                        disabled={saving}
                        type="submit"
                      >
                        {saving ? "Saving..." : "Save"}
                      </Button>
                    </>
                  )}
                  {!editing && (
                    <Button
                      fullWidth={true}
                      variant="outlined"
                      color="primary"
                      onClick={() => {
                        setEditing(true);
                      }}
                    >
                      Edit
                    </Button>
                  )}
                </Box>
              </Grid>
            </Grid>
          </form>
        )}
      </Formik>
    </Box>
  );
}
