import { Avatar, Box, Button, Grid } from "@material-ui/core";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import { navigate } from "@reach/router";
import axios from "axios";
import Color from "color";
import React, { ReactElement } from "react";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import Branding from "../../config/Branding";

interface Props {
  setUser: React.Dispatch<React.SetStateAction<IUser | undefined>>;
  user: IUser;
}

export default function TopNav(props: Props): ReactElement {
  const bg = Color(Branding.lightBg).alpha(0.8).string();
  const gridItemStyle: CSSProperties = {
    lineHeight: "40px",
  };
  const logoutClick = () => {
    axios
      .get(process.env.REACT_APP_API_ROOT + "/auth/logout", {
        withCredentials: true,
      })
      .then((response) => {
        props.setUser(undefined);
        navigate("/login");
      });
  };
  return (
    <Box position="fixed" top={0} right={0} left="16.66667%" zIndex={1000}>
      <Box p={2} bgcolor={bg}>
        <Grid container spacing={3} direction="row-reverse">
          <Grid item>
            <Box>
              <Grid container spacing={1}>
                <Grid item style={gridItemStyle}>
                  Welcome {props.user.fullName}
                </Grid>
                <Grid item style={gridItemStyle}>
                  <Button color="primary" onClick={logoutClick}>
                    Logout
                  </Button>
                </Grid>
                <Grid item>
                  <Avatar />
                </Grid>
              </Grid>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
}
