import { Box, Divider, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import check from "../../images/check.svg";

interface Props {
  bigText?: string;
  smallText?: string;
  children?: React.ReactNode;
}

export default function SuccessBigMessage(props: Props): ReactElement {
  return (
    <Box textAlign="center">
      <Box maxWidth={120} mx="auto" pb={3}>
        <img src={check} style={{ display: "block" }} alt="success" />
      </Box>
      <Typography variant="h5">Success!</Typography>
      <Box py={1}>
        <Typography variant="h6">{props.bigText}</Typography>
      </Box>
      <Box>
        <Typography>{props.smallText}</Typography>
      </Box>
      <Box p={2}>
        <Divider variant="middle" />
      </Box>
      <Box>{props.children}</Box>
    </Box>
  );
}
