import { Box, Button, Divider } from "@material-ui/core";
import { NavigateFn } from "@reach/router";
import { FormikErrors } from "formik";
import React, { ReactElement } from "react";
import FormErrors from "./FormErrors";

interface Props {
  title: string;
  navigate?: NavigateFn;
  navigateUrl?: string;
  type?: "button" | "submit" | "reset";
  disabled?: boolean;
  errors?: FormikErrors<any>;
}

export default function SubmitButton(props: Props): ReactElement {
  const navigateUrl =
    props.navigateUrl !== undefined ? props.navigateUrl : "success";
  return (
    <Box>
      <Box my={3}>
        <Divider />
      </Box>
      <Box
        my={3}
        textAlign="right"
        maxWidth={160}
        marginLeft="auto"
        // marginRight="auto"
      >
        {props.errors !== undefined && (
          <Box my={3}>
            <FormErrors errors={props.errors} />
          </Box>
        )}
        <Button
          fullWidth={true}
          variant="contained"
          color="primary"
          type={props.type}
          onClick={() => {
            if (props.navigate) {
              props.navigate(navigateUrl);
            }
          }}
          disabled={props.disabled}
        >
          {props.title}
        </Button>
      </Box>
    </Box>
  );
}
