import { Redirect, Router } from "@reach/router";
import { ReactElement, useState } from "react";
import DashboardPage from "../dashboard/DashboardPage";
import AdminPanelLayout from "../layouts/AdminPanelLayout";
import EmptySection from "./EmptySection";
import LoginLayout from "../layouts/LoginLayout";
import NotFoundPage from "../layouts/NotFoundPage";
import LoginPage from "../login/LoginPage";
import AddEditWorkerPage from "../workers/AddEditWorkerPage";
import MyRequestsPage from "../requests/MyRequestsPage";
import MyWorkersPage from "../workers/MyWorkersPage";
import UpdateRequestPage from "../requests/UpdateRequestPage";
import SuccessWorkerPage from "../workers/SuccessWorkerPage";
import SuccessRequestPage from "../requests/SuccessRequestPage";
import AgencyProcessTemplatesPage from "../settings/AgencyProcessTemplatesPage";
import { Box, Typography } from "@material-ui/core";
import { useEffect } from "react";
import axios from "axios";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import UnauthorizedPage from "../layouts/UnauthorizedPage";
import { UserRole } from "../../../../estreeh-common-library/build/types/UserRole";
import AgencySettingsPage from "../settings/AgencySettingsPage";
import SuccessAgencyPage from "../settings/SuccessAgencyPage";

interface Props {}

export default function MasterRouter(props: Props): ReactElement {
  const [loading, setLoading] = useState(true);
  const [user, setUser] = useState<IUser | undefined>(undefined);

  const getUser = () => {
    return axios.get<IResponse<IUser | undefined>>(
      process.env.REACT_APP_API_ROOT + "/auth/me",
      { withCredentials: true }
    );
  };

  useEffect(() => {
    console.log("Getting user");
    if (loading) {
      getUser().then((result) => {
        console.log("User received from API");
        const user = result.data.data;
        setUser(user);
        setLoading(false);
      });
    }
  }, [setUser, setLoading, loading]);

  const validRoles: UserRole[] = ["Agency", "Agency Manager"];
  const validUser =
    user !== undefined &&
    user.agencyId !== undefined &&
    validRoles.includes(user.role);

  if (loading) {
    return (
      <Box p={2} textAlign="center">
        <Typography variant="h6">Loading...</Typography>
      </Box>
    );
  } else {
    return (
      <Router primary={false}>
        <Redirect from="/" to={!validUser ? "/login" : "/panel"} noThrow />
        {!validUser && (
          <>
            <UnauthorizedPage default />
            <LoginLayout path="login">
              <LoginPage setUser={setUser} path="/" getUser={getUser} />
            </LoginLayout>
          </>
        )}
        {validUser && user !== undefined && user.agencyId !== undefined && (
          <>
            {/* <Redirect from="/login" to="/" /> */}
            <NotFoundPage default />
            <AdminPanelLayout path="panel" user={user} setUser={setUser}>
              <Redirect from="/" to="dashboard" noThrow />
              <DashboardPage path="dashboard" user={user} />
              <EmptySection path="my-workers">
                <MyWorkersPage path="/" />
                <AddEditWorkerPage path="add" />
                <AddEditWorkerPage path="edit/:id" />
                <SuccessWorkerPage path="success" />
              </EmptySection>
              <EmptySection path="my-requests">
                <MyRequestsPage path="/" />
                <UpdateRequestPage path="update" />
                <SuccessRequestPage path="success" />
              </EmptySection>
              <EmptySection path="agency/settings">
                <AgencySettingsPage default />
                <AgencyProcessTemplatesPage path="process-templates" />
                <SuccessAgencyPage path="success" />
              </EmptySection>
            </AdminPanelLayout>
          </>
        )}
        {/* <RootLayout default user={user}/> */}
      </Router>
    );
  }
}
